@import "https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@400;700&family=Merriweather:wght@400;700&family=Crimson+Text:wght@400;700&display=swap";
body {
  color: #0000;
  background-color: #d1b899;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

h2 {
  color: #31200a;
  justify-content: center;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: Merriweather, serif;
  font-size: 18px;
  font-weight: 600;
}

main {
  background-color: #d1b899;
  max-width: 1200px;
  height: auto;
  margin: auto;
  padding: 1rem;
}

.book-div {
  background-color: #d1b899;
  border-top: 2px solid #362004;
  gap: 1rem;
  height: auto;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
}

.book-reviews {
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  width: 100%;
  height: auto;
  display: grid;
}

.books {
  filter: grayscale(75%);
  border-radius: 20px;
  flex-direction: column;
  align-content: baseline;
  width: 100%;
  height: auto;
  padding: 1rem;
  transition: all .3s;
  display: grid;
}

.books:hover {
  filter: none;
  background-color: #c29e6e;
  transition: all .3s;
  transform: translateY(.5rem);
}

.book-cover {
  object-fit: cover;
  filter: grayscale(5%);
  border-radius: 15px;
  justify-content: center;
  width: 100%;
  height: 250px;
  padding: .5rem;
  display: flex;
}

@media (width <= 768px) {
  #book-div {
    padding: 1rem;
  }

  .book-reviews {
    grid-template-columns: repeat(2, 1fr);
  }

  h2 {
    padding: .5rem;
    font-size: 16px;
  }

  .books {
    padding: .5rem;
  }

  .book-cover {
    height: 200px;
  }
}

@media (width <= 480px) {
  .book-reviews {
    grid-template-columns: 1fr;
  }

  h2 {
    font-size: 14px;
  }

  .book-div {
    flex-direction: column;
  }
}
